:host {
  display: block;

  storm-button,
  storm-dropdown,
  div {
    vertical-align: middle;
  }

  .btn {
    &:hover:not(.active) {
      color: white;
    }

    &.btn-outline-primary:hover {
      background-color: #e91e63;
    }

    &.disabled {
      color: #7b809a;
      border-color: #7b809a;
      cursor: not-allowed;
    }
  }
}